import React, { Component, PropTypes } from 'react';
import classNames from 'classnames';
import { Util } from '../utils';

/**
 * @typedef {Object} Notification-Property
 * @property {string} id -   ID.
 * @property {string} className - css class.
 * @property {string|number} width -  width 
 * @property {string|number} height -  height 
 * @property {string|element|jQuery} appendTo -  append  element
 * @property {object} position -  position (appendTo  )
 *    - )
 *      ```
 *      position: {
 *          pinned: true,
 *          top: null,
 *          left: null,
 *          bottom: 20,
 *          right: 20
 *      }
 *      ```
 * @property {number} autoHideAfter=5000 -    (milliseconds)
 * @property {boolean} button=false -  hide button  
 * @property {function} onShow -     
 * @property {function} onHide -     
 */
const propTypes = {
    id: PropTypes.string,
    className: PropTypes.string,
    width: PropTypes.oneOfType([
        PropTypes.number,
        PropTypes.string
    ]),
    height: PropTypes.oneOfType([
        PropTypes.number,
        PropTypes.string
    ]),
    appendTo: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.object
    ]),
    position: PropTypes.object,
    autoHideAfter: PropTypes.number,
    button: PropTypes.bool,
    onShow: PropTypes.func,
    onHide: PropTypes.func
};

const defaultProps = {
    position: {
        pinned: true,
        top: null,
        left: null,
        bottom: 20,
        right: 20
    },
    autoHideAfter: 5000,
    button: false
};

/**
 *  
 * @example
 * <Puf.Notification className="class-name" autoHideAfter={0} button={false} />
 *
 * @desc
 *  Kendo Notification
 * ============
 *  -          
 *  - Notification  - {@link Notification-Property}
 * @extends {React.Component}
 *
*/
class Notification extends Component {
    /**
     * 
     * @private
     */
    constructor(props) {
        super(props);

        //  id
        this._id = props.id || Util.getUUID();
        //  Dom
        this._$notification = null;
        // 
        this._notification = null;

        //  
        this.onShow = this.onShow.bind(this);
        this.onHide = this.onHide.bind(this);
    }

    /**
     *    ( )
     * @private
     */
    componentDidMount() {
        //  
        this._createComponent();
    }

    /**
     *  
     * @private
     */
    _createComponent() {
        this._$notification = $('#' + this._id);
        this._notification = this._$notification.kendoNotification(this._configuration()).data('kendoNotification');

        // Event Bind
        this._bindEvent();
    }

    /**
     * @private
     */
    _configuration() {
        const { position, autoHideAfter, button } = this.props;

        let configuration = {
            position: position,
            autoHideAfter: autoHideAfter,
            button: button
        };

        return configuration;
    }

    /**
     * Event Bind
     * @private
     */
    _bindEvent() {
        const { onShow, onHide } = this.props;

        if(typeof onShow === 'function') {
            this._notification.bind('show', this.onShow);
        }

        if(typeof onHide === 'function') {
            this._notification.bind('hide', this.onHide);
        }
    }

    /**
     *  Show Event onShow props   .
     *      -  show  .
     *      - this.props.onShow(e, element) 
     *        element jQuery object 
     * @param {object} e - 
     */
    onShow(e) {
        var element = e.element;

        if(typeof this.props.onShow !== 'undefined') {
            this.props.onShow(e, element);
        }
    }

    /**
     *  Hide Event onHide props   .
     *      -  hide  .
     *      - this.props.onHide(e, element) 
     *        element jQuery object 
     * @param {object} e - 
     */
    onHide(e) {
        var element = e.element;

        if(typeof this.props.onHide !== 'undefined') {
            this.props.onHide(e, element);
        }
    }

    /**
     *  display .
     * @param {object|string|function} data required -   
     * @param {string} type - Built-in  Custom 
     *     - Built-in : info, success, warning, error
     */
    show(data, type) {
        this._notification.show(data, type);
    }
    
    /**
     *  hide .
     */
    hide() {
        this._notification.hide();
    }

    /**
     * @private
     */
    render() {
        //  
        const { className } = this.props;

        return (
            <span id={this._id} className={classNames(className)}></span>
        );
    }
}

Notification.propTypes = propTypes;
Notification.defaultProps = defaultProps;

export default Notification;


