import React, { Component, PropTypes } from 'react';
import classNames from 'classnames';
import { Util } from '../utils';

/**
 * @typedef {Object} Window-Property
 * @property {string} id -   ID.
 * @property {string} className - css class.
 * @property {string} title='Title' -   
 * @property {string} titleIconClassName='window-title-icon' -    
 * @property {string[]} actions=['Minimize','Maximize','Close'] -    
 *     - Minimize:  
 *     - Maximize:  
 *     - Close:  
 *     - Refresh:  
 * 
 * @property {string|number} width='50%' -  width 
 * @property {string|number} height='50%' -  height 
 * @property {number} minWidth=150 -  minWidth 
 * @property {number} minHeight=100 -  minHeight 
 * @property {boolean} visible=true -  show(true)/hide(false)
 * @property {boolean} modal=false -  modal 
 * @property {boolean} resizable=true -  resize  
 * @property {function} onOpen -  open    
 * @property {function} onClose -  close    
 * @property {function} onResize -  resize    
 * @property {function} onDragStart -  dragstart    
 * @property {function} onDragEnd -  dragend    
 * @property {function} onRefresh -  refresh    
 * @property {function} onActivate -  opening animation    
 * @property {function} onDeactivate -  closing animation    
 */
const propTypes = {
    id: PropTypes.string,
    className: PropTypes.string,
    title: PropTypes.string,
    titleIconClassName: PropTypes.string,
    actions: PropTypes.array,       // ['Pin', 'Refresh', 'Minimize', 'Maximize', 'Close']
    width: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number
    ]),
    height: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number
    ]),
    minWidth: PropTypes.number,
    minHeight: PropTypes.number,
    visible: PropTypes.bool,
    modal: PropTypes.bool,
    resizable: PropTypes.bool,
    onOpen: PropTypes.func,
    onClose: PropTypes.func,
    onResize: PropTypes.func,
    onDragStart: PropTypes.func,
    onDragEnd: PropTypes.func,
    onRefresh: PropTypes.func,
    onActivate: PropTypes.func,
    onDeactivate: PropTypes.func
};

const defaultProps = {
    title: 'Title',
    titleIconClassName: 'window-title-icon',
    actions: ['Minimize', 'Maximize', 'Close'], // Pin
    width: '50%',
    height: '50%',
    minWidth: 150, 
    minHeight: 100,
    visible: true,
    modal: false, 
    resizable: true
};

/**
 *  
 * @example
 * <Puf.Window width={500} height="50%" />
 *
 * @desc
 *  Kendo Window
 * ============
 *  - Window 
 *  - Window  - {@link Window-Property}
 * @extends {React.Component}
 *
*/
class Window extends Component {
    /**
     * 
     * @private
     */
    constructor(props) {
        super(props);

        //  id
        this._id = props.id || Util.getUUID();
        //  Dom
        this._$window = null;
        // 
        this._window = null;

        //  
        this.onOpen = this.onOpen.bind(this);
        this.onClose = this.onClose.bind(this);
        this.onResize = this.onResize.bind(this);
        this.onDragStart = this.onDragStart.bind(this);
        this.onDragEnd = this.onDragEnd.bind(this);
        this.onRefresh = this.onRefresh.bind(this);
        this.onActivate = this.onActivate.bind(this);
        this.onDeactivate = this.onDeactivate.bind(this);
    }

    /**
     *    ( )
     * @private
     */
    componentDidMount() {
        //  
        this._createComponent();
    }

    /**
     *   props   (    )
     * @private
     */
    componentWillReceiveProps(nextProps) {
        if(typeof nextProps.titleIconClassName !== 'undefined' && nextProps.titleIconClassName !== this.props.titleIconClassName) {
            this.setTitleIcon(nextProps.titleIconClassName);
        }
    }

    /**
     *  
     * @private
     */
    _createComponent() {
        this._$window = $('#' + this._id);
        this._window = this._$window.kendoWindow(this._configuration()).data('kendoWindow');

        const { className, titleIconClassName } = this.props;
        // render div k-window-content   dom.
        //  k-window  addClass .
        if(typeof className !== 'undefined') {
            this._$window.parent().addClass(className);
        }

        // title icon
        // this._window.wrapper.find('.k-window-title').prepend('<span class=' + titleIconClassName + '></span>');
        $('<span></span>').insertBefore(this._window.wrapper.find('.k-window-title')).addClass(titleIconClassName);

        // Event Bind
        this._bindEvent();
    }

    /**
     * @private
     */
    _configuration() {
        const { title, actions, width, height, minWidth, minHeight, visible, modal, resizable } = this.props;

        var configuration = {
            title: title,
            actions: actions,
            width: width,
            height: height,
            minWidth: minWidth,
            minHeight: minHeight,
            visible: visible,
            modal: modal,
            resizable: resizable
        };

        return configuration;
    }

    /**
     * Event Bind
     * @private
     */
    _bindEvent() {
        const { onOpen, onClose, onResize, onDragStart, onDragEnd, onRefresh, onActivate, onDeactivate } = this.props;

        if(typeof onOpen === 'function') {
            this._window.bind('open', this.onOpen);
        }

        if(typeof onClose === 'function') {
            this._window.bind('close', this.onClose);
        }

        if(typeof onResize === 'function') {
            this._window.bind('resize', this.onResize);
        }

        if(typeof onDragStart === 'function') {
            this._window.bind('dragstart', this.onDragStart);
        }

        if(typeof onDragEnd === 'function') {
            this._window.bind('dragend', this.onDragEnd);
        }
        
        if(typeof onRefresh === 'function') {
            this._window.bind('refresh', this.onRefresh);
        }

        if(typeof onActivate === 'function') {
            this._window.bind('activate', this.onActivate);
        }
        
        if(typeof onDeactivate === 'function') {
            this._window.bind('deactivate', this.onDeactivate);
        }
        
    }

    /**
     *  Open Event onOpen props   .
     *      -  open   .
     *      - this.props.onOpen(e) 
     * @param {object} e - 
     */
    onOpen(e) {
        if(typeof this.props.onOpen !== 'undefined') {
            this.props.onOpen(e);
        }
    }

    /**
     *  Close Event onClose props   .
     *      -  close   .
     *      - this.props.onClose(e) 
     * @param {object} e - 
     */
    onClose(e) {
        if(typeof this.props.onClose !== 'undefined') {
            this.props.onClose(e);
        }
    }

    /**
     *  Resize Event onResize props   .
     *      -  resize   .
     *      - this.props.onResize(e) 
     * @param {object} e - 
     */
    onResize(e) {
        if(typeof this.props.onResize !== 'undefined') {
            this.props.onResize(e);
        }
    }

    /**
     *  Dragstart Event onDragStart props   .
     *      -  dragstart   .
     *      - this.props.onDragStart(e) 
     * @param {object} e - 
     */
    onDragStart(e) {

        if(typeof this.props.onDragStart !== 'undefined') {
            this.props.onDragStart(e);
        }
    }

    /**
     *  Dragend Event onDragEnd props   .
     *      -  dragend   .
     *      - this.props.onDragEnd(e) 
     * @param {object} e - 
     */
    onDragEnd(e) {
        if(typeof this.props.onDragEnd !== 'undefined') {
            this.props.onDragEnd(e);
        }
    }

    /**
     *  Refresh Event onRefresh props   .
     *      -  refresh   .
     *      - this.props.onRefresh(e) 
     * @param {object} e - 
     */
    onRefresh(e) {
        if(typeof this.props.onRefresh !== 'undefined') {
            this.props.onRefresh(e);
        }
    }

    /**
     *  Activate Event onActivate props   .
     *      -  opening animation   .
     *      - this.props.onActivate(e) 
     * @param {object} e - 
     */
    onActivate(e) {
        if(typeof this.props.onActivate !== 'undefined') {
            this.props.onActivate(e);
        }
    }

    /**
     *  Deactivate Event onDeactivate props   .
     *      -  closing animation   .
     *      - this.props.onDeactivate(e) 
     * @param {object} e - 
     */
    onDeactivate(e) {
        if(typeof this.props.onDeactivate !== 'undefined') {
            this.props.onDeactivate(e);
        }
    }

    /**
     *  open
     * @return {Puf.Window} - Puf.Window 
     */
    open() {
        this._window.open();
        return this;
    }

    /**
     *  close
     * @return {Puf.Window} - Puf.Window 
     */
    close() {
        this._window.close();
        return this;
    }

    /**
     *  center  open.
     * @return {Puf.Window} - Puf.Window 
     */
    center() {
        this._window.center();
        return this;
    }

    /**
     *  x, y  .
     * @return {Puf.Window} - Puf.Window 
     */
    setPosition(x, y) {
        this._$window.offset({ left: x, top: y });
        return this;
    }

    /**
     *   
     * @return {string} -   
     */
    getTitle() {
        return this._window.title();
    }

     /**
     *   
     * @param {string} value -   
     */
    setTitle(value) {
        this._window.title(value);
    }

    /**
     *    
     * @param {string} value -   
     */
    setTitleIcon(value) {
        if(typeof value === 'string') {
            this._window.wrapper.find('.k-window-title').prev().attr('class', value);
        }
    }

    /**
     * @private
     */
    render() {
        //  
        const { className, children } = this.props;

        return (
            <div id={this._id}>{children}</div>
        );
    }
}

Window.propTypes = propTypes;
Window.defaultProps = defaultProps;

export default Window;


