import React, { Component, PropTypes } from 'react';
import classNames from 'classnames';
import { Util } from '../utils';

/**
 * @typedef {Object} Alert-Property
 * @property {string} id -   ID.
 * @property {string} className - css class.
 * @property {string} title -  
 * @property {string} okLabel -  OK Label
 * @property {string|number} width -  width 
 * @property {string|number} height -  height 
 * @property {function} onOpen -  open   
 * @property {function} onClose -  close   
 */
const propTypes = {
    id: PropTypes.string,
    className: PropTypes.string,
    title: PropTypes.string,
    okLabel: PropTypes.string,
    width: PropTypes.oneOfType([
        PropTypes.number,
        PropTypes.string
    ]),
    height: PropTypes.oneOfType([
        PropTypes.number,
        PropTypes.string
    ]),
    onOpen: PropTypes.func,
    onClose: PropTypes.func
};

const defaultProps = {
    title: 'Title',
    okLabel: $ps_locale.confirm,
    width: 300,
    height: 200
};

/**
 *  
 * @example
 * <Puf.Alert name="numericTextBox" />
 *
 * @desc
 *  Customize Kendo Dialog
 * ============
 *  - Alert 
 *  - Alert  - {@link Alert-Property}
 * @extends {React.Component}
 *
*/
class Alert extends Component {
    /**
     * 
     * @private
     */
    constructor(props) {
        super(props);

        //  id
        this._id = props.id || Util.getUUID();
        //  Dom
        this._$alert = null;
        // 
        this._alert = null;

        //  
        this.onOpen = this.onOpen.bind(this);
        this.onClose = this.onClose.bind(this);
    }

    /**
     *    ( )
     * @private
     */
    componentDidMount() {
        //  
        this._createComponent();
    }

    /**
     *  
     * @private
     */
    _createComponent() {
    	this._$alert = $('#' + this._id);
        this._alert = this._$alert.kendoDialog(this._configuration()).data('kendoDialog');

        // Event Bind
        this._bindEvent();
    }

    /**
     * @private
     */
    _configuration() {
        const { title, okLabel, width, height } = this.props;

        let configuration = {
            title: title,
            width: width,
            height: height,
            closable: false,
            modal: true,
            actions: [{
                text: okLabel,
                action: function(e) {
                    console.log(e.sender); // a reference to the dialog widget object
                    alert('OK action was clicked');

                    // false  dialog  .
                    return true;
                },
            }]
        };

        return configuration;
    }

    /**
     * Event Bind
     * @private
     */
    _bindEvent() {
        const { onOpen, onClose } = this.props;

        if(typeof onOpen === 'function') {
            this._alert.bind('open', this.onOpen);
        }

        if(typeof onClose === 'function') {
            this._alert.bind('close', this.onClose);
        }
    }

    /**
     *  Open Event onOpen props   .
     *      -  open  .
     *      - this.props.onOpen(e) 
     * @param {object} e - 
     */
    onOpen(e) {
        if(typeof this.props.onOpen !== 'undefined') {
            this.props.onOpen(e);
        }
    }

    /**
     *  Close Event onClose props   .
     *      -  close  .
     *      - this.props.onClose(e) 
     * @param {object} e - 
     */
    onClose(e) {
        if(typeof this.props.onClose !== 'undefined') {
            this.props.onClose(e);
        }
    }

    /**
     *  open
     * @return {Puf.Alert} - Puf.Alert 
     */
    open() {
        this._alert.open();
        return this;
    }

    /**
     *  close
     * @return {Puf.Alert} - Puf.Alert 
     */
    close() {
        this._alert.close();
        return this;
    }

    /**
     * @private
     */
    render() {
        //  
        const { className } = this.props;

        return (
            <div id={this._id} className={classNames(className)}></div>
        );
    }
}

Alert.propTypes = propTypes;
Alert.defaultProps = defaultProps;

export default Alert;


