import React, { Component, PropTypes } from 'react';
import classNames from 'classnames';
import { Util, DateUtil } from '../utils';

/**
 * @typedef {Object} DatePicker-Property
 * @property {string} id -   ID.
 * @property {string} className - css class.
 * @property {string} name - 
 * @property {number|string} width -  width 
 * @property {string|object} value -   
 *      - string: YYYY-MM-DD HH:mm:ss  
 *      - object: Date  
 * @property {string|object} minValue -    
 *      - string: YYYY-MM-DD HH:mm:ss  
 *      - object: Date  
 * @property {string|object} maxValue -    
 *      - string: YYYY-MM-DD HH:mm:ss  
 *      - object: Date  
 * @property {boolean} nowDate=true -  
 * @property {boolean} timePicker=false - () picker 
 * @property {string} dateFormat='yyyy-MM-dd' -   Format 
 * @property {string} timeFormat='HH:mm' -   Format 
 * @property {number} minInterval=5 -   
 * @property {boolean} disabled=false -   / 
 * @property {boolean} readOnly=false -  readonly 
 * @property {string} locale='ko_KR' -   
 * @property {function} onChange - change 
 * @property {function} onOpen - open 
 * @property {function} onClose - close 
 * @property {function} initCallback -      
 */
const propTypes = {
    id: PropTypes.string,
    className: PropTypes.string,
    name: PropTypes.string,
    width: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number
    ]),
    value: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.object
    ]),
    minValue: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.object
    ]),
    maxValue: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.object
    ]),
    nowDate: PropTypes.bool,
    timePicker: PropTypes.bool,
    dateFormat: PropTypes.string,
    timeFormat: PropTypes.string,
    minInterval: PropTypes.number,
    disabled: PropTypes.bool,
    readOnly: PropTypes.bool,
    locale: PropTypes.string,
    onChange: PropTypes.func,
    onClose: PropTypes.func,
    onOpen: PropTypes.func,
    initCallback: PropTypes.func
};

const defaultProps = {
    nowDate: true,
    timePicker: false,
    dateFormat: 'yyyy-MM-dd',
    timeFormat: 'HH:mm',
    readOnly: false,
    disabled: false,
    minInterval: 5,
    locale: "ko-KR"
};

/**
 *  
 * @example
 * <Puf.DatePicker name="DatePicker" />
 *
 * @desc
 *  Kendo DatePicker
 * ============
 *  -  
 *  - timePicker: true      
 *  - DatePicker  - {@link DatePicker-Property}
 * @todo Date, Time Format   resouce locale 
 * @extends {React.Component}
 *
*/
class DatePicker extends Component {
    /**
     * 
     * @private
     */
    constructor(props) {
        super(props);

        //  id
        this._id = props.id || Util.getUUID();
        //  Dom
        this._$datePicker = null;
        // 
        this._datePicker = null;

        //  
        this.onChange = this.onChange.bind(this);
        this.onClose = this.onClose.bind(this);
        this.onOpen = this.onOpen.bind(this);
    }

    /**
     *    ( )
     * @private
     */
    componentDidMount() {
        //  
        this._createComponent();       
    }
    
    /**
     *   props   (    )
     * @private
     */
    componentWillReceiveProps(nextProps) {
        if(typeof nextProps.value !== 'undefined' && nextProps.value !== this.props.value) {
            this.setValue(nextProps.value);
        }

        // disabled
        if(typeof nextProps.disabled !== 'undefined' && nextProps.disabled !== this.props.disabled) {
            this.setEnable(!nextProps.disabled);
        }

        // readOnly
        if(typeof nextProps.readOnly !== 'undefined' && nextProps.readOnly !== this.props.readOnly) {
            this.setReadOnly(nextProps.readOnly);
        }
    }

    /**
     *  
     * @private
     */
    _createComponent() {
        const { timePicker, readOnly, disabled, initCallback } = this.props;

        this._$datePicker = $('#' + this._id);
        if(timePicker) {
            this._datePicker = this._$datePicker.kendoDateTimePicker(this._configuration()).data('kendoDateTimePicker');
        }else {
            this._datePicker = this._$datePicker.kendoDatePicker(this._configuration()).data('kendoDatePicker');
        }

        this.setEnable(!disabled);
        this.setReadOnly(readOnly);

        // DateRangePicker ,    datepicker   
        if(typeof initCallback === 'function') {
            let component = {
                _instance: this._getInstance(),
                _datePicker: this.getComponent(),
                _$datePicker: this.getComponentDom()
            };
            initCallback(component);
        }

        // Event Bind
        this._bindEvent();
    }

    /**
     *  
     * @private
     */
    _configuration() {
        const {value, nowDate, timePicker, dateFormat, timeFormat, timeInterval, minValue, maxValue, locale} = this.props;

        //  render value  
        let dateValue;
        if(value === undefined) {
            if(nowDate){
                dateValue = new Date();
            }
        }else if(typeof value === 'string' || typeof value.getMonth === 'function') {
            dateValue = value;
        }

        let configuration = {
            value: dateValue,
            format: (timePicker) ? dateFormat + ' ' + timeFormat : dateFormat,
            culture: locale
        };

        //  Picker
        if(timePicker) {
            $.extend(configuration, {
                timeFormat,
                interval: timeInterval
            });
        }

        //  
        if(minValue !== undefined) {
            $.extend(configuration, {min: minValue});
        }

        //  
        if(maxValue !== undefined) {
            $.extend(configuration, {max: maxValue});
        }
        return configuration;
    }

    /**
     * Event Bind
     * @private
     */
    _bindEvent() {
        const { onChange, onOpen, onClose } = this.props;

        if(typeof onChange === 'function') {
            this._datePicker.bind('change', this.onChange);
        }

        if(typeof onOpen === 'function') {
            this._datePicker.bind('open', this.onOpen);
        }

        if(typeof onClose === 'function') {
            this._datePicker.bind('close', this.onClose);
        }
    }

    /**
     * ()  
     * @private
     * @return {object} DatePicker () .
     */
    _getInstance() {
        return this;
    }
  
    /**
     *  Change Event onChange props   .
     * @param {object} e - 
     */
    onChange(e) {
        if(typeof this.props.onChange === 'function') {
            var date = this.getValue();
            this.props.onChange(date);
        }
    }

    /**
     * Picker Open Event onOpen props   .
     * @param {object} e - 
     */
    onOpen(e) {
        if(typeof this.props.onOpen === 'function') {
            this.props.onOpen(e);
        }
    }

    /**
     * Picker Close Event onClose props   .
     * @param {object} e - 
     */
    onClose(e) {
        if(typeof this.props.onClose === 'function') {
            this.props.onClose(e);
        }
    }

    /**
     *  
     * @return {object} DatePicker  .
     */
    getComponent(){
        return this._datePicker;
    }

    /**
     *  Dom 
     * @return {object} DatePicker Dom 
     */
    getComponentDom(){
        return this._$datePicker;
    }

    /**
     *  
     */
    open() {
        this._datePicker.open();
    }

    /**
     *  
     */
    close() {
        this._datePicker.close();
    }

    /**
     *   
     * @return {string} - (: 'YYYY-MM-DD HH:MM') 
     */
    getValue() {
        const value = this._datePicker.value();
        return DateUtil.getDateToString(value);    
    }

    /**
     *   
     * @param {string|object} value -   
     *      - string: YYYY-MM-DD HH:mm:ss  
     *      - object: Date  
     */
    setValue(value) {
        this._datePicker.value(value);
    }

    /**
     *   
     * @param {string|object} value -   
     *      - string: YYYY-MM-DD HH:mm:ss  
     *      - object: Date  
     */
    setMinValue(value) {
        this._datePicker.min(value);
    }

    /**
     *   
     * @param {string|object} value -   
     *      - string: YYYY-MM-DD HH:mm:ss  
     *      - object: Date  
     */
    setMaxValue(value) {
        this._datePicker.max(value);
    }

    /**
     *  /  
     * @param {boolean} isBool
     */
    setEnable(isBool) {
        this._datePicker.enable(isBool);
    }

    /**
     *      
     * @param {boolean} isBool
     */
    setReadOnly(isBool) {
        this._datePicker.readonly(isBool);
    }
    
    /**
     * @private
     */
    render() {
        const { name, className, width } = this.props;
        const inputStyle = {
            width
        };
        return (
            <input id={this._id} name={name} className={classNames(className)} style={inputStyle} />
        );
    }
}

DatePicker.propTypes = propTypes;
DatePicker.defaultProps = defaultProps;

export default DatePicker;


