import React, { Component, PropTypes } from 'react';
import classNames from 'classnames';
import { Util, KendoUtil } from '../utils';


/**
 * @typedef {Object} AutoComplete-Property
 * @property {string} id -   ID.
 * @property {string} name - 
 * @property {string|number} width='100%' -  width 
 * @property {string} className - css class
 * @property {string} method='POST' -   http method
 * @property {object} params -    
 * @property {object} responseKey -      
 *    -     List totalCount   .
 *      ```
 *      responseKey: {
 *          list: 'response.list',
 *          totalCount: 'response.totalCount'
 *      }
 *      ```
 * @property {string} placeholder -       text
 * @property {array|object} items -  ,      
 * @property {object} template -   ()
 * @property {string} filter='startswith' -  scope 
 * @property {string} separator=',' -   .  
 * @property {number} minLength=1 -  ( )
 * @property {string} textField - data text  field
 * 
 */
const propTypes = {
    id: PropTypes.string,
    name: PropTypes.string,
    className: PropTypes.string,
    url: PropTypes.string,
    method: PropTypes.string,
    params: PropTypes.object,
    responseKey: PropTypes.object,
    placeholder: PropTypes.string,
    items: PropTypes.oneOfType([
        PropTypes.array,
        PropTypes.object
    ]),
    template: PropTypes.string,
    filter: PropTypes.string,
    separator: PropTypes.string,
    minLength: PropTypes.number,
    textField: PropTypes.string
}

const defaultProps = {
    width: '100%',
    method: 'POST', 
    responseKey: {
        list: null,
        totalCount: null,
    },
    placeholder: $ps_locale.autoComplete, 
    filter: 'startswith', 
    separator: ',', 
    minLength: 1,
    items: []
};

/**
 *  
 * @example
 * <Puf.AutoComplete name="AutoComplete" responseKey={{list: "data", totalCount: "count"}} />
 *
 * @desc
 *  Kendo AutoComplete
 * ============
 *  - ( )     
 *  - AutoComplete  - {@link AutoComplete-Property}
 * @extends {React.Component}
 *
*/
class AutoComplete extends Component {
    /**
     * 
     * @private
     */
    constructor(props) {
        super(props);

        //  id
        this._id = props.id || Util.getUUID();
        //  Dom
        this._$autoComplete = null;
        // 
        this._autoComplete = null;
        //   
        this._totalCount = null;
    }

    /**
     *    ( )
     * @private
     */
    componentDidMount() {
        //  
        this._createComponent();    
    }

    /**
     *   
     * @private
     */
    static get _CONST() {
        return {
            _DATA_TYPE: 'json',
            _CONTENT_TYPE: 'application/json; charset=utf-8'
        }
    }
	
    /**
     *  
     * @private
     */
    _createComponent() {
        this._$autoComplete = $('#' + this._id);
        this._autoComplete = this._$autoComplete.kendoAutoComplete(this._configuration(this.props));
    }
    
    /**
     *  
     * @private
     */
    _configuration() {
        const { template, textField, minLength, separator } = this.props;
        const dataSource = this._dataSource();

        const configuration = {
            dataSource,
            template,
            minLength,
            separator,
            dataTextField: textField
        };
        return configuration;
    }

    /**
     * DataSource 
     * @private
     */
    _dataSource(props) {
        const { url, method, items, params, responseKey } = this.props;

        let dataSource;
        if(url) {
            dataSource = new kendo.data.DataSource({
                transport: {
                    read: {
                        url: url,
                        type: method,
                        data: params,      
                        dataType: AutoComplete._CONST._DATA_TYPE,
                        contentType: AutoComplete._CONST._CONTENT_TYPE
                    },
                    parameterMap: function(data, type) {
                        return JSON.stringify(data);
                    }
                },
                schema: {
                    data: function(response) {
                        if(responseKey && responseKey.list) {
                            response = KendoUtil.setResponseKey(response, responseKey.list);
                        }
                        return response;
                    },
                    total: function(response) {
                        if(responseKey && responseKey.totalCount) {
                            response = KendoUtil.setResponseKey(response, responseKey.totalCount);
                        }else{
                            if(responseKey && responseKey.list) {
                                response = KendoUtil.setResponseKey(response, responseKey.list).length;
                            }else{
                                response = response.length;
                            }
                        }
                        this._totalCount = response;
                        return response;
                    }
                },
                serverFiltering: true
            });
        }else {
            dataSource = items;
        }
        return dataSource;
    }

    /**
     *  
     * @return {object} AutoComplete  .
     */
    getComponent() {
        return this._autoComplete;
    }

    /**
     *  Dom 
     * @return {object} AutoComplete Dom 
     */
    getComponentDom() {
        return this._$autoComplete;
    }

    /**
     * @private
     */
    render() {
        const { name, width, className, placeholder } = this.props;
        const inputStyle = {
            width
        };
        return (
            <input id={this._id} name={name} className={classNames(className)} style={inputStyle} placeholder={placeholder} />
        );
    }
}

AutoComplete.propTypes = propTypes;
AutoComplete.defaultProps = defaultProps;

export default AutoComplete;


