import React, { Component, PropTypes } from 'react';
import classNames from 'classnames';
import { Util } from '../utils';

/**
 * @typedef {Object} ProgressBar-Property
 * @property {string} id -   ID.
 * @property {string} className - css class.
 * @property {string} type='value' - ProgressBar    (value, percent, chunk)
 * @property {number} value=0 -  
 * @property {number|boolean|object} animation={duration:600} -  
 * @property {boolean} disabled=false -   / 
 * @property {number} min=0 -      
 * @property {number} max=100 -      
 * @property {string} orientation='horizontal' -   (horizontal, vertical)
 * @property {function} onChange -       
 * @property {function} onComplete -   maximum     
 */
const propTypes = {
    id: PropTypes.string,
    className: PropTypes.string,
    type: PropTypes.oneOf(['value', 'percent', 'chunk']),
    value: PropTypes.number,
    animation: PropTypes.oneOfType([
        PropTypes.number,
        PropTypes.bool,
        PropTypes.object
    ]),
    disabled: PropTypes.bool,
    min: PropTypes.number,
    max: PropTypes.number,
    orientation: PropTypes.oneOf(['horizontal', 'vertical']),
    onChange: PropTypes.func,
    onComplete: PropTypes.func
};

const defaultProps = {
    type: 'value', 
    value: 0, 
    animation: { duration: 600 }, 
    disabled: false,
    min: 0,
    max: 100,
    orientation: 'horizontal'
};

/**
 *  
 * @example
 * <Puf.ProgressBar value={80} />
 *
 * @desc
 *  Kendo ProgressBar
 * ============
 *  -     
 *  - ProgressBar  - {@link ProgressBar-Property}
 * @extends {React.Component}
 *
*/
class ProgressBar extends Component {
    /**
     * 
     * @private
     */
    constructor(props) {
        super(props);

        //  id
        this._id = props.id || Util.getUUID();
        //  Dom
        this._$progressBar = null;
        // 
        this._progressBar = null;

        //  
        this.onChange = this.onChange.bind(this);
        this.onComplete = this.onComplete.bind(this);
    }

    /**
     *    ( )
     * @private
     */
    componentDidMount() {
        //  
        this._createComponent();
    }

    /**
     *  
     * @private
     */
    _createComponent() {
        this._$progressBar = $('#' + this._id);
        this._progressBar = this._$progressBar.kendoProgressBar(this._configuration()).data('kendoProgressBar');

        // Event Bind
        this._bindEvent();
    }

    /**
     * @private
     */
    _configuration() {
        const { type, value, animation, disabled, min, max, orientation } = this.props;

        // animation
        var _animation;
        if(typeof animation === 'number') {
            _animation = { duration: animation };
        }else if(animation === true) {
            _animation = { duration: 600 };
        }else {
            _animation = animation;
        }

        var configuration = {
            type: type,
            value: value,
            animation: _animation,
            enable: !disabled,
            min: min,
            max: max,
            orientation: orientation
        };

        return configuration;
    }

    /**
     * Event Bind
     * @private
     */
    _bindEvent() {
        const { onChange, onComplete } = this.props;

        if(typeof onChange === 'function') {
            this._progressBar.bind('change', this.onChange);
        }

        if(typeof onComplete === 'function') {
            this._progressBar.bind('complete', this.onComplete);
        }
    }

    /**
     *  Change Event onChange props   .
     *      -   .
     *      - this.props.onChange(e, value) 
     * @param {object} e - 
     */
    onChange(e) {
        if(typeof this.props.onChange !== 'undefined') {
            this.props.onChange(e, e.value);
        }
    }

    /**
     *  Complete Event onComplete props   .
     *      -   .
     *      - this.props.onComplete(e, value) 
     * @param {object} e - 
     */
    onComplete(e) {
        if(typeof this.props.onComplete !== 'undefined') {
            this.props.onComplete(e, e.value);
        }
    }
    
    /**
     *   
     * @return {number} -  
     */
    getValue() {
        return this._progressBar.value();
    }

     /**
     *   
     * @param {number} value -  
     */
    setValue(value) {
        this._progressBar.value(value);
    }

    /**
     *  /  
     * @return {boolean} - (true)/(false) 
     */
    getEnable() {
        return this._progressBar.enable();
    }

    /**
     *  /  
     * @param {boolean} isBool
     */
    setEnable(isBool) {
        this._progressBar.enable(isBool);
    }

    /**
     * @private
     */
    render() {
        //  
        const { className } = this.props;

        return (
            <div id={this._id} className={classNames(className)}></div>
        );
    }
}

ProgressBar.propTypes = propTypes;
ProgressBar.defaultProps = defaultProps;

export default ProgressBar;


