import React, { Component, PropTypes } from 'react';
import classNames from 'classnames';
import { Util } from '../utils';

/**
 * @typedef {Object} Slider-Property
 * @property {string} id -   ID.
 * @property {string} className - css class.
 * @property {string} value=0 -   
 * @property {string|number} width='100%' -  width 
 * @property {boolean} disabled=false -   / 
 * @property {number} min=0 -      
 * @property {number} max=10 -      
 * @property {string} orientation='horizontal' -   (horizontal, vertical)
 * @property {string} dragHandleTitle='drag' - Slider   
 * @property {boolean} showButtons=true - Slider /  show/hide
 * @property {number} largeStep=5 -      
 * @property {number} smallStep=1 -      
 * @property {string} tickPlacement='both' - Slider tick   (topLeft, bottomRight, both, none)
 *     - topLeft: tick  Slider orientation  horizontal   top, vertical   left 
 *     - bottomRight: tick  Slider orientation  horizontal   bottom, vertical   right 
 *     - both: tick  Slider   
 *     - none: tick   
 * 
 * @property {boolean} tooltipEnabled=true - Slider tooltip enable(true)/disable(false)
 * @property {string} tooltipFormat='{0:#,#.##}' - Slider tooltip  
 * @property {string} tooltipTemplate - Slider tooltip 
 * @property {function} onChange -       
 * @property {function} onSlide -       
 */
const propTypes = {
    id: PropTypes.string,
    className: PropTypes.string,
    value: PropTypes.number,
    width: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number
    ]),
    disabled: PropTypes.bool,
    min: PropTypes.number,
    max: PropTypes.number,
    orientation: PropTypes.oneOf(['horizontal','vertical']).isRequired,
    dragHandleTitle: PropTypes.string,
    showButtons: PropTypes.bool,
    largeStep: PropTypes.number,
    smallStep: PropTypes.number,
    tickPlacement: PropTypes.oneOf(['topLeft','bottomRight','both','none']).isRequired,
    tooltipEnabled: PropTypes.bool,
    tooltipFormat: PropTypes.string,
    tooltipTemplate: PropTypes.string,
    onChange: PropTypes.func,
    onSlide: PropTypes.func
};

const defaultProps = {
    value: 0,
    width: '100%',
    disabled: false,
    min: 0,
    max: 10,
    orientation: 'horizontal',
    dragHandleTitle: 'drag',
    showButtons: true,
    largeStep: 5,
    smallStep: 1,
    tickPlacement: 'both',
    tooltipEnabled: true,
    tooltipFormat: '{0:#,#.##}'
};

/**
 *  
 * @example
 * <Puf.Slider value={3} />
 *
 * @desc
 *  Kendo Slider
 * ============
 *  - Slider 
 *  - Slider  - {@link Slider-Property}
 * @extends {React.Component}
 *
*/
class Slider extends Component {
    /**
     * 
     * @private
     */
    constructor(props) {
        super(props);
        
        //  id
        this._id = props.id || Util.getUUID();
        //  Dom
        this._$slider = null;
        // 
        this._slider = null;

        //  
        this.onChange = this.onChange.bind(this);
        this.onSlide = this.onSlide.bind(this);
    }

    /**
     *    ( )
     * @private
     */
    componentDidMount() {
        //  
        this._createComponent();
    }

    /**
     *   props   (    )
     * @private
     */
    componentWillReceiveProps(nextProps) {
        // disabled
        if(typeof nextProps.disabled !== 'undefined' && nextProps.disabled !== this.props.disabled) {
            this.setEnable(!nextProps.disabled);
        }
    }

    /**
     *  
     * @private
     */
    _createComponent() {
        const { disabled } = this.props;

        this._$slider = $('#' + this._id);
        this._slider = this._$slider.kendoSlider(this._configuration()).data('kendoSlider');

        this.setEnable(!disabled);
        
        // Event Bind
        this._bindEvent();
    }

    /**
     * @private
     */
    _configuration() {
        const { value, min, max, orientation, dragHandleTitle, showButtons, largeStep, smallStep, tickPlacement, tooltipEnabled, tooltipFormat, tooltipTemplate } = this.props;

        var configuration = {
            value: value,
            min: min,
            max: max,
            orientation: orientation,
            dragHandleTitle: dragHandleTitle,
            showButtons: showButtons,
            largeStep: largeStep,
            smallStep: smallStep,
            tickPlacement: tickPlacement,
            tooltip: {
                enabled: tooltipEnabled,
                format: tooltipFormat
            }
        };

        // tooltip template
        if(typeof tooltipTemplate === 'string') {
            $.extend(true, configuration, { tooltip: { template: tooltipTemplate } });
        }

        return configuration;
    }

    /**
     * Event Bind
     * @private
     */
    _bindEvent() {
        const { onChange, onSlide } = this.props;

        if(typeof onChange === 'function') {
            this._slider.bind('change', this.onChange);
        }

        if(typeof onSlide === 'function') {
            this._slider.bind('slide', this.onSlide);
        }
    }

    /**
     *  Change Event onChange props   .
     *      -   .
     *      - this.props.onChange(e, value) 
     * @param {object} e - 
     */
    onChange(e) {
        if(typeof this.props.onChange === 'function') {
            this.props.onChange(e, e.value);
            //e.stopImmediatePropagation();
        }
    }

    /**
     *  Slide Event onSlide props   .
     *      -   .
     *      - this.props.onSlide(e, value) 
     * @param {object} e - 
     */
    onSlide(e) {
        if(typeof this.props.onSlide === 'function') {
            this.props.onSlide(e, e.value);
            //e.stopImmediatePropagation();
        }
    }

    /**
     *   
     * @return {number} -  
     */
    getValue() {
        return this._slider.value();
    }

     /**
     *   
     * @param {number} value -  
     */
    setValue(value) {
        this._slider.value(value);
    }

    /**
     *  /  
     * @return {boolean} - (true)/(false) 
     */
    getEnable() {
        return this._slider.enable();
    }

    /**
     *  /  
     * @param {boolean} isBool
     */
    setEnable(isBool) {
        this._slider.enable(isBool);
    }
    
    /**
     *  width  
     * @param {number|string} value - width  
     */
    setWidth(value) {
        let width = '0px';
        if(typeof value === 'number') {
            width = value + 'px';
        }else if(typeof value === 'string') {
            width = value;
        }

        this._slider.wrapper.css('width', width);
        this._slider.resize();
    }

    /**
     *  Max  
     * @return {number} - Max  
     */
    getMax() {
        return this._slider.max();
    }

    /**
     *  max  
     * @param {number|string} value - max  
     */
    setMax(value) {
        this._$slider.prev().find('a').attr('aria-valuemax', value);
        // this._slider.max(value);
    }

    /**
     *  Min  
     * @return {number} - Min  
     */
    getMin() {
        return this._slider.min();
    }

    /**
     *  min  
     * @param {number|string} value - min  
     */
    setMin(value) {
        this._$slider.prev().find('a').attr('aria-valuemin', value);
        // this._slider.min(value);
    }

    /**
     * DOM  Slider 
     */
    destroy() {
        var wrapper = this._slider.wrapper,
            element = this._slider.element;

        this._slider.destroy();
        wrapper.before(element.show());
        wrapper.remove();

        // this._slider.destroy();
        // this._$slider.closest('.k-slider').remove();
    }

    /**
     * Slider 
     */
    create(_configuration) {
        var configuration = this._configuration();
        $.extend(true, configuration, _configuration);
        this._slider = this._$slider.kendoSlider(configuration).data('kendoSlider');

        // Event Bind
        this._bindEvent();
    }

    /**
     * @private
     */
    render() {
        //  
        const { className, width } = this.props;
        const inputStyle = {
            width
        };

        return (
            <input id={this._id} className={classNames(className)} style={inputStyle} />
        );
    }
}

Slider.propTypes = propTypes;
Slider.defaultProps = defaultProps;

export default Slider;


